@import '../../Public/tools.scss';

.p-nav--logged_out {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 70px;
  border-bottom: 1px solid $dark_blue;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 $spacing_100;
  color: $dark_blue;
  font-weight: bold;
  font-size: 18px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.p-nav__item {
  height: 100%;
  border-left: 1px solid $dark_blue;
  padding: $spacing_150;
  text-decoration: none !important;
  color: inherit !important;

  &:last-child {
    padding-right: 0;
  }
}



